<template>
	<div id="notice">
		<commonHeader :title="'系统公告'"></commonHeader>
			
		<div class="list-wrap">
			<div class="container" v-for="(item,index) in ads" @click="toActivity(item)">
				<div class="notice-img">
					<img :src="item.imagePath" alt=""/>
				</div>
				<div class="notice-info">
						<div class="notice-name">{{item.actionName}}</div>
						<div class="notice-date">{{item.sendTime}}</div>
				</div>
			</div>
		</div>	

	</div>
</template>

<script>
	
export default {
	data(){
		return {
			ads:[], 
		}
	},
  components: {
  	
  },
  created(){
  	this.getAdList()
  },
  methods:{
  	getAdList(){
	    this.$Api.getAdList(9).then((res) => {
	    	if(res.q.s == 0){
	    		console.log(res)
	    		this.ads = res.q.ads
	    	}
    	})
    },
    toActivity(item){
    	if(item.actionType == 1 || item.actionType == 2 || item.actionType == 3){
				window.location.href = item.actionContent
			}else if(item.actionType == 5 || item.actionType == 9){
				this.$router.push({name : 'ArticleDetails',params:{id:item.actionContent,type:'notice'}})
			}
    },
  }
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#notice{
		height: 100%;
		background: #f0f0f0;
		.top{
			height: 1.65rem;
			padding: 0.4rem;
			border-bottom: 1px solid #ccc;
			background: #fff;
			.top-text{
				font-size: 0.37rem;
				color: #666;
			}

		}
		.container{
			position: relative;
			display: flex;
			height: 2.93rem;
			padding: 0.27rem 0.4rem;
			border-bottom: 1px solid $border-color;
			background: #fff;
			
			.notice-img {
				width: 3.2rem;
				height: 2.4rem;
				>img {
					width: 3.2rem;
					height: 2.4rem;
				}
			}
			.notice-info {
				position: relative;
				flex: 1;
				line-height: 1;
				margin-left: 0.35rem;
				text-align: left;
				font-size: 0;
				.notice-name {
					font-size: 0.4rem;
					line-height: 1.286;
					@include textflow-two;
					margin-bottom: 0.27rem;
					color: #333;
				}
				.notice-date{
					position: absolute;
					font-size: 0.37rem;
					color: #777;
					right: 0;
					bottom: 0.13rem;
				}
				
			}
		}
	}
</style>


